.nav-container{
    /*background-color: #acacac;
    */background-color: #27273f;
    height: 100vh;
    width: 12rem;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    
}
ul{
    padding: 0 !important;
}
li{
    list-style-type: none;
    padding: 1rem 2rem;
}
a{
    color: rgb(255, 255, 255);
    text-decoration: none;
}
.list-container{
    display: flex;
    flex-direction: row;
    justify-content: left;
    align-items: center;
}
.nav-icons{
    font-size: 1.4rem;
    margin-right: 8px;
}

.all-navbar-container{
    position: fixed;
}
.login{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    bottom: 10%;
}
.login-icon{
    font-size: 3rem;
    color: white;
}
.subject-container{
    padding-left: 16rem;
    color: black;
}
.big-header{
    font-size: 4rem;
    margin-bottom: 2rem;
}
.add-button-container{
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 2rem 0;
}
.add-button-container .add-button{
    font-size: 1.5rem;
}
.card-container{
    position: relative;
    font-family: 'Rubik', sans-serif;
    background-color: #FF8B64;
    height: 18rem;
    width: 18rem;
    overflow: hidden;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    margin: 2rem 0;
}
.card-inside-container{
    position: absolute;
    background-color: #1D204B;
    width: 100%;
    margin-top: 3rem;
    height: 85%;
    color: white;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    z-index: 2;
    padding-left: 20px;
    padding-top: 24px;
    
}
.card-header{
    display: flex;
    justify-content: space-between;
    border-bottom: none;
    
}
.card-body{
    border-bottom: none;
    padding-bottom: 0;
}
.card-footer{
    border-top: none;
    padding-top: 0;
    color: rgb(141, 141, 141);
}
.card-header-text{
    font-weight: 300;
}
.card-body-text{
    
    font-size: 4rem;   
    font-weight: 400;
}
.card-body-text h1{
    margin-bottom: 0;
}
.card-body-text-min{
    font-weight: 300;
    font-size: 1rem;
}
.icon{
    position:absolute;
    right: 1rem;
    top: -5%;
    z-index: 0;
}
.delete-icon-container{
    background-color: transparent;
    border: none;
    position: absolute;
    right: 2%;
    bottom: 5%;
}
.delete-icon{

}
.center{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.timer-button{
    padding: 0.5rem 1.5rem;
}
.timer-icon{
    font-size: 24px;
}
.button-content{
    display: flex !important;
    flex-direction: row;
    align-items: center;
}
.subject-button{
    color: white !important;
    background-color: transparent !important;
}

.timer-container{
    background-color: white;
}
.btn{
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
}
.inner-circle{
    position: relative;
  background-color: white;
  height: 13rem;
  width: 13rem;
  border-radius: 50%;
  border:none;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
}

.circle-content{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
}
.login-container{
    background-color: white;
    padding: 0rem 3rem 4rem 3rem;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border-radius: 20px;
    -webkit-border-radius: 20px;
    -moz-border-radius: 20px;
    -ms-border-radius: 20px;
    -o-border-radius: 20px;
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, -50%);
    -webkit-transform: translate(-50%, -50%);
    -moz-transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    -o-transform: translate(-50%, -50%);
    z-index: 999;
}
